.main_parent {
    display: grid;
    grid-template-columns: 1fr;
    padding: 10px;
    gap: 50px;
}

.country_box {
    border: 7px solid #1e2b34;
    /* width: 80%;  */
    background-color: #2b3743;
    justify-self: center;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
    color: white;
    padding-bottom: 70px;
    cursor: pointer;
}

.country_box>p {
    font-weight: bold;
}

.country_box>p>span {
    font-weight: normal;
}



.country_box img {
    margin-bottom: 25px;
}

.country_box>h3,
p {
    padding-left: 15px;
}

.country_box>h3 {
    margin-bottom: 25px;
}


/* media query for desktop */
@media screen and (min-width: 768px) {
    .main_parent {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;

    }

    .country_box {
        width: 20%;
    }

    .country_box img {
        height: 150px;
    }
}