.single__parent {
    padding: 20px;
    color: #fefefe;
}

.btn {
    padding: 10px 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    background-color: #2b3743;
    border: none;
    outline: none;
    color: white;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
    margin-top: 1rem;
    cursor: pointer;
}

.single__box {
    margin-top: 5rem;
    border: 7px solid #1e2b34;
    background-color: #2b3743;
    display: flex;
    flex-direction: column;
    justify-content: center;

}

.single__box h1 {
    font-size: 1.5em;
    padding-left: 15px;
    margin: 1rem 0 1rem 0;
}

.paras_first {
    line-height: 2;
}

.paras_first>p>span {
    color: rgba(255, 255, 255, 0.735);
}

.paras_second {
    line-height: 2;
    margin-top: 1rem;
}

.paras_second>p>span {
    color: rgba(255, 255, 255, 0.735);
}

/* Media query for desktop */

@media screen and (min-width: 768px) {
    .single__box {
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        padding: 20px;
    }

}