.header_main {
    background-color: #2b3743;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fefefe;
    padding: 30px 15px 30px 15px;
    box-shadow: 0 2px 20px #2b3743;
    transition: all .3s ease;
}

.logo h2 {
    font-size: 1em;
}

/* 
.logo a {
    color: white;
} */

.main_logo {
    color: white;
    text-decoration: none;
}

.main_logo_change {
    color: black;
    text-decoration: none;
}

.mode_switch {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.mode_switch span {
    margin-right: 7px;
    cursor: pointer;
}

.backGroundColorChange {
    background-color: white;
    color: black;
    box-shadow: 0 2px 20px rgba(17, 17, 17, 0.249);
    transition: all .3s ease;
}

/* 
.logo_change {
    color: black;
} */




/* media query for desktop */
@media screen and (min-width: 768px) {
    .header_main {
        justify-content: space-between;
    }

    .logo h2 {
        font-size: 1.5em;
    }

    .logo {
        margin-left: 5rem;
    }

    .mode_switch {
        margin-right: 5rem;
    }

}