.search_main {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr;
    gap: 10px;
    padding: 10px;
    margin-top: 1.5rem;
}

.search_box {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2b3743;
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
}

.search_box span {
    color: white;
    margin-left: 25px;
}


.search_box input[type="search"] {
    width: 100%;
    height: 50px;
    border: none;
    padding: 20px;
    background: transparent;
    color: #fefefe;
    outline: none;
}

.search_box input[type="search"]::placeholder {
    color: #fefefe;
}

.filter_box>select {
    width: 180px;
    padding: 15px 15px;
    height: 50px;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: #2b3743;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    outline: none;
}


/* Style for desktop */

@media screen and (min-width: 768px) {
    .search_main {
        width: 90%;
        margin: auto;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        margin-top: 30px;
        margin-bottom: 30px;
    }

    .search_box {
        width: 60%;
    }

    .search_box input[type="search"] {
        width: 100%;
    }

    .filter_box {
        position: relative;
    }

    .filter_box>select {
        position: absolute;
        right: 0;
    }
}